@import 'reset/reset.css';
@lost gutter 28px;
body {
  lost-utility: edit;
}
html {
  color: #000;
  padding: 28px;
  height: auto;
  overflow-y: scroll;
}
header {
  text-align: center;
  margin: 28px 0 56px;
}
div {
  height: auto;
  text-align: right;
  margin-bottom: 14px;
  margin-top: 14px;
}
div div {
  margin-top: 0;
  margin-bottom: 0;
}
div .item {
  min-width: 280px;
  height: auto;
  float: left;
  margin-bottom: 26px;
}
p:first-child {
  text-align: left;
  margin-top: 26px;
}
section {
  lost-utility: edit;
  lost-center: 1204px flex;
  lost-flex-container: row;
  margin-top: 28px;
  margin-bottom: 28px;
}
:last-of-type {
  margin-bottom: 0;
}
:last-of-type div {
  lost-column: 1/1 flex;
}
:last-of-type div:nth-of-type(1) {
  background: #fff;
}
:last-of-type div:nth-of-type(1) div {
  lost-column: 1/1 flex;
  background: #fff;
}
@media (min-width: 588px) {
  :last-of-type div:nth-of-type(1) div {
    lost-column: 1/2 flex;
  }
}
@media (min-width: 896px) {
  :last-of-type div:nth-of-type(1) div {
    lost-column: 1/3 flex;
  }
}
@media (min-width: 1204px) {
  :last-of-type div:nth-of-type(1) div {
    lost-column: 1/4 flex;
  }
}
